import { useRouter } from 'next/router';
import { useState, useEffect } from 'react';
import axios from 'axios';

const Result = () => {
  const router = useRouter();
  const { inspectionID } = router.query; // ดึง inspectionID จาก URL
  const [result, setResult] = useState(null);

  useEffect(() => {
    if (inspectionID) {
      // ดึงข้อมูลจาก backend โดยใช้ inspectionID
      axios.get(`http://localhost:8000/history/${inspectionID}`)
        .then(response => {
          setResult(response.data);
        })
        .catch(err => {
          console.error('Error fetching result: ', err);
        });
    }
  }, [inspectionID]);

  if (!result) {
    return <div>Loading...</div>;
  }

  return (
    <div>
      <h1>Result for Inspection ID: {inspectionID}</h1>
      <h2>Basic Information</h2>
      <p><strong>Create Date - Time:</strong> {new Date(result.createDate).toLocaleString()}</p>
      <p><strong>Inspection ID:</strong> {result.inspectionID}</p>
      <p><strong>Standard:</strong> {result.standardName}</p>
      <p><strong>Total Sample:</strong> {result.totalSample}</p>
      <p><strong>Note:</strong> {result.note}</p>
      <p><strong>Price:</strong> {result.price}</p>
      <p><strong>Sampling Date/Time:</strong> {new Date(result.samplingDate).toLocaleString()}</p>
      <p><strong>Sampling Point:</strong> {result.samplingPoint.join(', ')}</p>

      <h2>Inspection Result</h2>
      <table>
        <thead>
          <tr>
            <th>Name</th>
            <th>Length</th>
            <th>Actual</th>
          </tr>
        </thead>
        <tbody>
          {result.inspectionResult.composition.map((item, index) => (
            <tr key={index}>
              <td>{item.name}</td>
              <td>{item.length}</td>
              <td>{item.actual}</td>
            </tr>
          ))}
        </tbody>
      </table>

      <button onClick={() => router.push('/create-inspection')}>Back to Create Inspection</button>
      <button onClick={() => router.push(`/edit-result/${inspectionID}`)}>Edit</button>
    </div>
  );
};

export default Result;
